<template>
    <div class="container">
        <h1>{{ $t("title.passwordGenerator") }}</h1>
        <p class="lead">{{ $t("description.passwordGenerator") }}</p>
        <b>{{ $t("text.optionAtLeastOne") }}</b>
        <div class="form-group">
            <div class="form-check form-check-inline">
                <input class="form-check-input" type="checkbox" id="inlineCheckbox1" value="option1" v-model="inputType[0]">
                <label class="form-check-label" for="inlineCheckbox1">{{ $t("text.upEnglish") }}</label>
            </div>
            <div class="form-check form-check-inline">
                <input class="form-check-input" type="checkbox" id="inlineCheckbox2" value="option2" v-model="inputType[1]">
                <label class="form-check-label" for="inlineCheckbox2">{{ $t("text.lowEnglish") }}</label>
            </div>
            <div class="form-check form-check-inline">
                <input class="form-check-input" type="checkbox" id="inlineCheckbox3" value="option3" v-model="inputType[2]">
                <label class="form-check-label" for="inlineCheckbox3">{{ $t("text.digital") }}</label>
            </div>
            <div class="form-check form-check-inline">
                <input class="form-check-input" type="checkbox" id="inlineCheckbox4" value="option4" v-model="inputType[3]">
                <label class="form-check-label" for="inlineCheckbox4">{{ $t("text.symbol") }}</label>
            </div>
            <div class="form-check form-check-inline">
                <input class="form-check-input" type="checkbox" id="inlineCheckbox5" value="option5" v-model="noConfuseType">
                <label class="form-check-label" for="inlineCheckbox5">{{ $t("text.noConfusingWords") }}</label>
            </div>
        </div>
        <div class="form-group">
            <div for="customRange1">{{ $t("text.length") }}</div>
            <input type="number" v-model="length" style="width:100px;"/>
            <input type="range" class="custom-range" min="0" max="100" step="1" v-model="length" id="customRange1">
        </div>
        <div class="form-group col-12 col-md-3 col-lg-2">
            <button type="button" class="btn btn-sm btn-primary btn-block" @click="generatorPassword">{{
                $t("text.generate")}}
            </button>
        </div>
        <div class="form-group">
            <InputHasCopy :value="answer"></InputHasCopy>
        </div>
    </div>
</template>

<script>
    import InputHasCopy from "../../components/elements/InputHasCopy.vue";

    export default {
        components: {
            InputHasCopy
        },
        metaInfo() {
            return {
                title: this.$i18n.t('title.passwordGenerator'),
                meta: []
            }
        },
        data() {
            return {
                length: 20,
                inputType: [true, true, true, false],
                inputTypeWeight: [37, 37, 18, 8],
                noConfuseType: false,
                answer: ""
            }
        },
        methods: {
            generatorPassword() {
                let self = this;
                let data = [
                    "ABCDEFGHIJKLMNOPQRSTUVWXYZ",
                    "abcdefghijkLmnopqrstuvwxyz",
                    "1234567890",
                    "!@#$%^&*"
                ];
                let noChar = ["OLVUI", "ogqvu", "01", ""];
                let randomAnswer = "";
                let answerQuantity = "";

                if (self.noConfuseType) {
                    for (let i = 0; i < 4; i++) {
                        for (let j = 0; j < noChar[i].length; j++) {
                            data[i] = data[i].replace(noChar[i].charAt(j), "");
                        }
                    }
                }

                if (!isNaN(self.length) && self.length > 0) {
                    answerQuantity = self.allocatedQuantity(data);

                    for (let i = 0; i < answerQuantity.length; i++) {
                        let randomType = parseInt(answerQuantity.charAt(i));
                        let randomData = data[randomType];
                        let random = Math.floor(Math.random() * randomData.length);
                        randomAnswer += randomData.charAt(random);
                    }
                    self.answer = randomAnswer;
                }
            },
            allocatedQuantity() {
                let self = this;
                let canRandomType = "";
                let randomString = "";
                let totalWeight = 0;
                let inputTypeWeight = [];

                for (let i = 0; i < self.inputTypeWeight.length; i++) {
                    inputTypeWeight[i] = self.inputTypeWeight[i];
                }

                for (let i = 0; i < self.inputType.length; i++) {
                    if (self.inputType[i]) {
                        canRandomType += i.toString();
                        totalWeight += inputTypeWeight[i];
                    } else {
                        inputTypeWeight[i] = 0;
                    }
                }

                while (randomString.length < self.length) {
                    let randomArray = [];
                    for (let i = 0; i < inputTypeWeight.length; i++) {
                        for (let j = 0; j < inputTypeWeight[i]; j++) {
                            randomArray.push(i);
                        }
                    }
                    let randomNumber = Math.floor(Math.random() * totalWeight);
                    randomString += randomArray[randomNumber];
                    if (randomString.length === self.length) {
                        for (let i = 0; i < canRandomType.length; i++) {
                            let char = canRandomType.charAt(i);
                            if (randomString.indexOf(char) === -1) {
                                randomString = "";
                                break;
                            }
                        }
                    }
                }
                return randomString;
            },
            copy() {
                let Url2=document.getElementById("passwordGenerator");
                Url2.select(); // 選擇物件
                document.execCommand("Copy"); // 執行瀏覽器複製命令
                alert("已複製好，可貼粘。");
            }
        }
    }
</script>

